.tooltip {
  position: relative;
  font-size: 0;
  &__marker {
    display: inline-block;
    cursor: pointer;
    border: 2px solid $secondary;
    border-radius: 50%;
    font-size: 0;
    position: relative;
    width: 14px;
    height: 14px;
    transition: all .3s ease-in-out;
    padding: 6px;
    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    path {
      transition: fill .3s ease-in-out;
    }
    &:focus {
      outline: none;
    }
    &--active,
    &:focus,
    &:hover {
      background-color: $secondary;
    }
    &--active,
    &:focus path,
    &:hover path {
      fill: $white;
    }
    &--active + .tooltip__popup {
      opacity: unset;
    }
  }
  &__popup {
    display: inline-block;
    padding: 5px;
    width: 157px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 25px;
    background-color:$black;
    font-weight: 400;
    font-size: 9px;
    line-height: 14px;
    text-align: center;
    color: $white;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  &__image {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -6px;
  }
}
.tooltip-box {
  position: absolute;
  font-size: 0;
  bottom: 0;
  right: 15%;
}
@media (max-width: 1024px) {
  .tooltip-box {
    left: 20%;
  }
}
